import React from "react";

const Todo = ({ todo, handleToggle }) => {
    const handleClick = (e) => {
        e.preventDefault();
        handleToggle(e.currentTarget.id);
        console.log(e.currentTarget.id);
    };
    return (
        <div
            id={todo.id}
            key={todo.id + todo.task}
            className={todo.complete ? "todo strike" : "todo"}
            onClick={handleClick}
            value={todo.id}
        >
            {todo.task}
        </div>
    );
};

export default Todo;
