//importing hooks from the react
import { useState } from "react";
//importing components
import Header from "./component/Header/Header";
import Todoform from "./component/Todoform/Todoform";
import Todolist from "./component/Todolist/Todolist";
//importing hard coded data
import data from "./data.json";
//styling for the App super component
import "./App.css";

function App() {
    //using the useState hook and intializing it with data
    const [toDoList, setToDoList] = useState(data);

    //toggle function to change the todo to true or false
    const handleToggle = (id) => {
        let mapped = toDoList.map((task) => {
            return task.id === Number(id)
                ? { ...task, complete: !task.complete }
                : { ...task };
        });
        setToDoList(mapped);
    };

    //deleting todo function
    const handleFilter = () => {
        let flitered = toDoList.filter((task) => {
            return !task.complete;
        });
        setToDoList(flitered);
    };

    //adding new todo functions
    const addTask = (userInput) => {
        let copy = [...toDoList];
        copy = [
            ...copy,
            { id: toDoList.length + 1, task: userInput, complete: false },
        ];
        setToDoList(copy);
    };
    //returning
    return (
        <div className="app">
            <div className="container">
                <Header />
                <Todoform addTask={addTask} />
                <Todolist
                    toDoList={toDoList}
                    handleFilter={handleFilter}
                    handleToggle={handleToggle}
                />
            </div>
        </div>
    );
}

export default App;
